import { defineComponent, ref, reactive } from 'vue';
import localCache from '@/utils/cache'; // 编写好规则，e-plus是默认集成async-validator这个库的
// 匹配规则单独抽到一个文件

import { rules } from '../config/account-config';
export default defineComponent({
  setup() {
    const account = reactive({
      // 如果有缓存，就用缓存的，没有就用空字符串
      name: localCache.getCache('name') ?? '',
      psw: localCache.getCache('psw') ?? ''
    });
    /* 登录逻辑 */
    // InstanceType<typeof 组件> 这是很常见的,获取组件对象类型的方法

    const formRef = ref();

    const loginAction = isKeepPsw => {
      // 1.校验。上面是输入限制只是会报红字，并不会限制登录
      // 校验关键：拿到 form 组件对象后调用里面的方法
      formRef.value?.validate(valid => {
        if (valid) {
          // 2.记住密码
          if (isKeepPsw) {
            // 缓存,建议写在utils里面，我们专门的工具
            localCache.setCache('name', account.name);
            localCache.setCache('psw', account.psw);
          } else {
            localCache.deleteCache('name');
            localCache.deleteCache('psw');
          } // 3.开始登录验证


          console.log('登录成功');
        }
      });
    };

    return {
      account,
      rules,
      loginAction,
      formRef
    };
  }

});